.management-panel {
  font-family: Arial, sans-serif;
}

.panel-content {
  display: flex;
}

.user-list-section,
.hierarchy-section {
  padding: 16px;
}

.droppable-list {
  margin: 8px;
  padding: 8px;
  border: 1px solid gray;
  min-width: 200px;
  background-color: #f0f0f0;
}

.draggable-item {
  user-select: none;
  padding: 16px;
  margin: 0 0 8px 0;
  background-color: #fff;
  color: #333;
}

.hierarchy-tree {
  display: flex;
  flex-direction: column;
}

.tree-node {
  position: relative;
  margin-left: 16px;
}

.tree-node::before {
  content: "";
  position: absolute;
  top: -10px;
  left: -20px;
  border-left: 2px solid gray;
  border-bottom: 2px solid gray;
  width: 20px;
  height: 20px;
}

.tree-node-content {
  margin-left: 20px;
}

.branch {
  margin-left: 20px;
  padding-left: 20px;
  border-left: 2px solid gray;
}
.swal2-container {
  z-index: 2000;
}
.ProjectOperationEdit {
  width: calc(50% - 20px);
  margin-right: 5px;
}
.no-scrollbar::-webkit-scrollbar {
  display: none;
}

.no-scrollbar {
  -ms-overflow-style: none;
  scrollbar-width: none;
}